            ></path>
          </svg>
          <i>{{ every.playCount | formatCount }}</i>
        </span>
       </router-link>
     </div>
  </div>
</template>

<script>
export default {
  name: "TopList",
  components: {},
  data() {
    return {
      top_list1: [],
      top_list2: [],
      top_list3: [],

    };
  },
  created() {
    this.$axios.get("/toplist/detail").then((data) => {
      this.top_list1 = data.data.list.slice(0,4);
      this.top_list2 = data.data.list.slice(5,11);
      this.top_list3 = data.data.list.slice(12,17);
      this.top_list3.push(data.data.list[19]);
      // console.log(data.data.list);
    });
  },
  filters: {
    formatCount(val) {
      if (val / 10000 > 10000) {
        return parseInt(val / 100000000) + "亿";
      } else {
        return parseInt(val / 10000) + "万";
      }
    },

  },
  methods:{
      back(){
          this.$router.back();
      }
  }
};
</script>

